import { isStyleProp, isSplProp, isEvProp, isDynProp, getVm } from './utils';
import { isFunc, emptyObj } from '../utils';
import { patchStyle } from './patchStyle';
import { patchEvent } from './patchEvent';
import { isStream, hookStream } from './addons/stream';
import { devNotify } from "./addons/devmode";
import { XLINK_NS } from './defineSvgElement';

const XLINKHREF = "xlink:href";

export function remAttr(node, name, asProp) {
	if (asProp)
		node.el[name] = "";
	else {
		if (name === XLINKHREF)
			node.el.removeAttributeNS(XLINK_NS, "href");
		else
			node.el.removeAttribute(name);
	}
}

// setAttr
// diff, ".", "on*", bool vals, skip _*, value/checked/selected selectedIndex
export function setAttr(node, name, val, asProp, initial) {
	var el = node.el;

	if (val == null)
		!initial && remAttr(node, name, false);		//, asProp?  // will also removeAttr of style: null
	else if (node.ns != null) {
		if (name === XLINKHREF)
			el.setAttributeNS(XLINK_NS, "href", val);
		else
			el.setAttribute(name, val);
	}
	else if (name === "class")
		el.className = val;
	else if (name === "id" || typeof val === "boolean" || asProp)
		el[name] = val;
	else if (name[0] === ".")
		el[name.substr(1)] = val;
	else
		el.setAttribute(name, val);
}

export function patchAttrs(vnode, donor, initial) {
	const nattrs = vnode.attrs || emptyObj;
	const oattrs = donor.attrs || emptyObj;

	if (nattrs === oattrs) {
		if (_DEVMODE)
			devNotify("REUSED_ATTRS", [vnode]);
	}
	else {
		for (var key in nattrs) {
			var nval = nattrs[key];
			var isDyn = isDynProp(vnode.tag, key);
			var oval = isDyn ? vnode.el[key] : oattrs[key];

			if (FEAT_STREAM) {
				if (isStream(nval))
					nattrs[key] = nval = hookStream(nval, getVm(vnode));
			}

			if (nval === oval) {}
			else if (isStyleProp(key))
				patchStyle(vnode, donor);
			else if (isSplProp(key)) {}
			else if (isEvProp(key))
				patchEvent(vnode, key, nval, oval);
			else
				setAttr(vnode, key, nval, isDyn, initial);
		}

		// TODO: handle key[0] === "."
		// should bench style.cssText = "" vs removeAttribute("style")
		for (var key in oattrs) {
			!(key in nattrs) &&
			!isSplProp(key) &&
			remAttr(vnode, key, isDynProp(vnode.tag, key) || isEvProp(key));
		}
	}
}